import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home'
import Anunt from '../views/Anunt'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/anunt/:id',
    name: 'Anunt',
    component: Anunt
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
