<template>
  <div class="p-d-inline-block div-card">
    <Card>
      <template #title> Simple Card </template>
      <template #header>
        <router-link :to="{ name: 'Anunt', params: { id: data.id } }">
          <img
            :alt="data.altText"
            :src="
              `${publicPath}` + 'images/cardPresentationImages/' + data.image
            "
            class="card-image"
          />
        </router-link>
      </template>
      <template #content>
        <p>
          {{ data.description }}
        </p>
      </template>
    </Card>
  </div>
</template>

<script>
export default {
  name: "desktopCardComponent",
  props: ["data"],
  data() {
    return {
      publicPath: process.env.BASE_URL,
    };
  },
};
</script>
<style scoped>
.card-image {
  width: auto !important;
  height: 200px;
}
.p-card-header {
  text-align: center;
}
.div-card {
  width: 100%;
}
.p-card {
  margin: 0.5rem;
  border: 1px solid #dee2e6;
}
</style>