<template>
  <div class="p-grid nested-grid">
    <div class="p-col-2"></div>
    <div class="p-col-8">
      <div class="p-grid">
        <div class="p-col-12">
          <div class="p-d-inline-block div-card">
            <Card>
              <template #title> Simple Card </template>
              <template #header>
                <div class="p-d-flex">
                  <div>
                    <img
                      :alt="data.altText"
                      :src="
                        `${publicPath}` +
                        'images/cardPresentationImages/' +
                        data.image
                      "
                      class="card-image-anunt-type"
                    />
                  </div>
                  <div class="p-jc-center">
                    <img
                      :alt="data.altText"
                      :src="`${publicPath}` + 'images/testMap.png'"
                      class="card-image-anunt-map"
                    />
                  </div>
                </div>
              </template>
              <template #content>
                <p>
                  {{ data.description }}
                </p>
              </template>
            </Card>
          </div>
        </div>
      </div>
    </div>
    <div class="p-col-2"></div>
  </div>
</template>

<script>
export default {
  name: "anuntElementDesktop",
  props: ["data"],
  components: {},
  data() {
    return {
      publicPath: process.env.BASE_URL,
    };
  },
};
</script>
<style scoped>
.card-image-anunt-type {
  width: auto !important;
  height: 200px;
}
.card-image-anunt-map {
  width: auto !important;
  height: 200px;
}
.div-card {
  width: 100%;
}
</style>